<template>
  <div class="row justify-content-center">
    <div class="col text-center">
      <div class="login-form">
        <div class="card">
          <div class="card-header text-white" style="background: #042331">
            <h3>Register</h3>
          </div>
          <div class="card-body">
            <div
              class="form"
              autocomplete="off"
              method="post"
              action="/auth/login"
            >
              <div class="form-group">
                <input
                  class="form-control"
                  placeholder="Username"
                  type="mail"
                  name="email"
                  v-model="email"
                />
              </div>

              <div class="form-group">
                <input
                  class="form-control"
                  placeholder="Password"
                  type="password"
                  name="password"
                  v-model="password"
                />
              </div>

              <div class="form-group">
                <input
                  class="form-control"
                  placeholder="Confirm Password"
                  type="password"
                  name="password"
                  v-model="confirmPassword"
                />
              </div>

              <button
                class="btn btn-dark"
                style="background: #1ab187"
                @click="signIn()"
              >
                Registrarse
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import store from "../store/index";
import config from "../config";
export default {
  data: () => {
    return {
      email: "",
      password: "",
      confirmPassword: "",
    };
  },
  mounted() {
    if (this.$store.getters.isLoggedIn) {
      this.$router.replace(config.VUE_APP_BASE_URL);
    }
  },
  methods: {
    signIn() {
      store
        .dispatch("signIn", { email: this.email, password: this.password })
        .then(() => {
          this.$router.replace(config.VUE_APP_BASE_URL);
        });
    },
  },
};
</script>
<style>
.login-form {
  margin-top: 10%;
  min-width: 300px;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
}
</style>
